
import { computed, defineComponent, ref } from 'vue'
import { MutationTypes, useStore } from '@/store'
import { createRandomCode } from '@/utils/common'
import useScreening from '@/hooks/useScreening'
import useSlideHandler from '@/hooks/useSlideHandler'
import useHistorySnapshot from '@/hooks/useHistorySnapshot'

import { message } from 'ant-design-vue'

const hotkeys = [
  {
    type: '',
    children: [
      { label: '', value: 'Ctrl + X' },
      { label: '', value: 'Ctrl + C' },
      { label: '', value: 'Ctrl + V' },
      { label: '', value: 'Ctrl + D' },
      { label: '', value: 'Ctrl + A' },
      { label: '', value: 'Ctrl + Z' },
      { label: '', value: 'Ctrl + Y' },
      { label: '', value: 'Delete' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: 'Ctrl + F' },
      { label: '', value: ' / ' },
      { label: '', value: ' /  / Enter / Space' },
      { label: '', value: 'ESC' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: 'Enter' },
      { label: '', value: 'Ctrl + ' },
      { label: '', value: 'Ctrl + =' },
      { label: '', value: 'Ctrl + -' },
      { label: '', value: 'Ctrl + 0' },
      { label: '', value: ' / ' },
      { label: '', value: ' / ' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: ' /  /  / ' },
      { label: '', value: 'Ctrl + L' },
      { label: '', value: 'Ctrl + G' },
      { label: '', value: 'Ctrl + Shift + G' },
      { label: '', value: 'Alt + F' },
      { label: '', value: 'Alt + B' },
      { label: '', value: ' Ctrl  Shift' },
      { label: '', value: ' Ctrl  Shift' },
      { label: ' / ', value: ' Ctrl  Shift' },
      { label: '', value: 'Enter' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: 'Tab' },
      { label: '', value: 'Ctrl + ' },
      { label: '', value: 'Ctrl + ' },
      { label: '', value: 'Ctrl + ' },
      { label: '', value: 'Ctrl + ' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: 'Ctrl + B' },
      { label: '', value: 'Ctrl + I' },
      { label: '', value: 'Ctrl + U' },
      { label: '', value: 'Ctrl + D' },
    ],
  },
]

export default defineComponent({
  name: 'editor-header',
  setup() {
    const store = useStore()

    const { enterScreening, enterScreeningFromStart } = useScreening()
    const { createSlide, deleteSlide } = useSlideHandler()
    const { redo, undo } = useHistorySnapshot()

    const showGridLines = computed(() => store.state.showGridLines)
    const toggleGridLines = () => {
      store.commit(MutationTypes.SET_GRID_LINES_STATE, !showGridLines.value)
    }

    const resetSlides = () => {
      store.commit(MutationTypes.SET_ACTIVE_ELEMENT_ID_LIST, [])
      store.commit(MutationTypes.SET_SLIDES, [{
        id: createRandomCode(),
        elements: [],
      }])
    }

    const openDoc = () => {
      message.warning('...')
    }

    const hotkeyDrawerVisible = ref(false)

    return {
      enterScreening,
      enterScreeningFromStart,
      createSlide,
      deleteSlide,
      redo,
      undo,
      toggleGridLines,
      showGridLines,
      resetSlides,
      openDoc,
      hotkeyDrawerVisible,
      hotkeys,
    }
  },
})
