<template>
  <div class="editor-header">
    <div class="left">
      <Dropdown :trigger="['click']">
        <div class="menu-item"><IconEdit /> </div>
        <template #overlay>
          <Menu>
            <MenuItem @click="undo()"></MenuItem>
            <MenuItem @click="redo()"></MenuItem>
            <MenuItem @click="createSlide()"></MenuItem>
            <MenuItem @click="deleteSlide()"></MenuItem>
            <MenuItem @click="toggleGridLines()">{{ showGridLines ? '' : '' }}</MenuItem>
            <MenuItem @click="resetSlides()"></MenuItem>
          </Menu>
        </template>
      </Dropdown>
      <Dropdown :trigger="['click']">
        <div class="menu-item"><IconPpt /> </div>
        <template #overlay>
          <Menu>
            <MenuItem @click="enterScreeningFromStart()"></MenuItem>
            <MenuItem @click="enterScreening()"></MenuItem>
          </Menu>
        </template>
      </Dropdown>
      <Dropdown :trigger="['click']">
        <div class="menu-item"><IconHelpcenter /> </div>
        <template #overlay>
          <Menu>
            <MenuItem @click="openDoc()"></MenuItem>
            <MenuItem @click="hotkeyDrawerVisible = true"></MenuItem>
          </Menu>
        </template>
      </Dropdown>
    </div>

    <div class="right">
      <Tooltip :mouseLeaveDelay="0" title="">
        <div class="menu-item" @click="enterScreening()">
          <IconPpt size="18" fill="#666" style="margin-top: 2px;" />
        </div>
      </Tooltip>
      <Tooltip :mouseLeaveDelay="0" title="Github ">
        <div class="menu-item"><IconGithub size="18" fill="#666" /></div>
      </Tooltip>
    </div>

    <Drawer
      width="320"
      placement="right"
      :visible="hotkeyDrawerVisible"
      @close="hotkeyDrawerVisible = false"
    >
      <div class="hotkeys">
        <template v-for="item in hotkeys" :key="item.type">
          <div class="title">{{item.type}}</div>
          <div class="hotkey-item" v-for="hotkey in item.children" :key="hotkey.label">
            <div class="label">{{hotkey.label}}</div>
            <div class="value">{{hotkey.value}}</div>
          </div>
        </template>
      </div>
    </Drawer>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from 'vue'
import { MutationTypes, useStore } from '@/store'
import { createRandomCode } from '@/utils/common'
import useScreening from '@/hooks/useScreening'
import useSlideHandler from '@/hooks/useSlideHandler'
import useHistorySnapshot from '@/hooks/useHistorySnapshot'

import { message } from 'ant-design-vue'

const hotkeys = [
  {
    type: '',
    children: [
      { label: '', value: 'Ctrl + X' },
      { label: '', value: 'Ctrl + C' },
      { label: '', value: 'Ctrl + V' },
      { label: '', value: 'Ctrl + D' },
      { label: '', value: 'Ctrl + A' },
      { label: '', value: 'Ctrl + Z' },
      { label: '', value: 'Ctrl + Y' },
      { label: '', value: 'Delete' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: 'Ctrl + F' },
      { label: '', value: ' / ' },
      { label: '', value: ' /  / Enter / Space' },
      { label: '', value: 'ESC' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: 'Enter' },
      { label: '', value: 'Ctrl + ' },
      { label: '', value: 'Ctrl + =' },
      { label: '', value: 'Ctrl + -' },
      { label: '', value: 'Ctrl + 0' },
      { label: '', value: ' / ' },
      { label: '', value: ' / ' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: ' /  /  / ' },
      { label: '', value: 'Ctrl + L' },
      { label: '', value: 'Ctrl + G' },
      { label: '', value: 'Ctrl + Shift + G' },
      { label: '', value: 'Alt + F' },
      { label: '', value: 'Alt + B' },
      { label: '', value: ' Ctrl  Shift' },
      { label: '', value: ' Ctrl  Shift' },
      { label: ' / ', value: ' Ctrl  Shift' },
      { label: '', value: 'Enter' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: 'Tab' },
      { label: '', value: 'Ctrl + ' },
      { label: '', value: 'Ctrl + ' },
      { label: '', value: 'Ctrl + ' },
      { label: '', value: 'Ctrl + ' },
    ],
  },
  {
    type: '',
    children: [
      { label: '', value: 'Ctrl + B' },
      { label: '', value: 'Ctrl + I' },
      { label: '', value: 'Ctrl + U' },
      { label: '', value: 'Ctrl + D' },
    ],
  },
]

export default defineComponent({
  name: 'editor-header',
  setup() {
    const store = useStore()

    const { enterScreening, enterScreeningFromStart } = useScreening()
    const { createSlide, deleteSlide } = useSlideHandler()
    const { redo, undo } = useHistorySnapshot()

    const showGridLines = computed(() => store.state.showGridLines)
    const toggleGridLines = () => {
      store.commit(MutationTypes.SET_GRID_LINES_STATE, !showGridLines.value)
    }

    const resetSlides = () => {
      store.commit(MutationTypes.SET_ACTIVE_ELEMENT_ID_LIST, [])
      store.commit(MutationTypes.SET_SLIDES, [{
        id: createRandomCode(),
        elements: [],
      }])
    }

    const openDoc = () => {
      message.warning('...')
    }

    const hotkeyDrawerVisible = ref(false)

    return {
      enterScreening,
      enterScreeningFromStart,
      createSlide,
      deleteSlide,
      redo,
      undo,
      toggleGridLines,
      showGridLines,
      resetSlides,
      openDoc,
      hotkeyDrawerVisible,
      hotkeys,
    }
  },
})
</script>

<style lang="scss" scoped>
.editor-header {
  background-color: #fff;
  user-select: none;
  border-bottom: 1px solid $borderColor;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}
.left, .right {
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-item {
  font-size: 13px;
  margin: 0 10px;
  cursor: pointer;
}

.hotkeys {
  height: 100%;
  overflow: auto;
  font-size: 12px;
}
.title {
  font-size: 14px;
  font-weight: 700;
  border-bottom: 1px solid #e5e5e5;
  padding: 15px 0 5px 0;
}
.hotkey-item {
  border-bottom: 1px solid #e5e5e5;
  padding: 15px 0 5px 0;
  display: flex;
  align-items: center;
}
.label {
  width: 140px;

  @include ellipsis();
}
</style>